import { useState, useEffect, useContext } from "react";
import { useSnackbar } from "notistack";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Slide from "@material-ui/core/Slide";
import { useHistory } from "react-router";
import Question from "../Components/Question";
import QuestionList from "../Components/QuestionList";
import SessionContext from "../SessionContext";
import Timer from "../Components/Timer";
import utils from "../utils";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginTop: "1%",
  },
}));

const MainPage = () => {
  const classes = useStyles();
  const [list, setList] = useState([]);
  const [activeQuestion, setActiveQuestion] = useState(false);
  const { sessionData, setSessionData } = useContext(SessionContext);
  const [score, setScore] = useState(sessionData.score || 0);
  const [time, setTime] = useState(null);

  const { enqueueSnackbar } = useSnackbar();
  const history = useHistory();

  const getNextQuestionId = (data, id) => {
    if (!id) return data[0]._id;
    const index = list.findIndex((question) => question._id == id);
    if (index == list.length - 1) return -1;
    return list[index + 1]._id;
  };

  useEffect(() => {
    utils
      .getList()
      .then((data) => {
        setList(data);

        const nextId =
          sessionData.currentQuestion || getNextQuestionId(data, null);

        if (nextId === -1) {
          utils.endContest().then((res) => {
            console.log(res);
            history.push("/finish");
          });
          return;
        }

        utils.getQuestion(nextId, null, null).then((qData) => {
          const message = "Time Starts Now!";
          if (nextId == data[0]._id) {
            enqueueSnackbar(message, {
              anchorOrigin: {
                vertical: "top",
                horizontal: "center",
              },
              variant: "info",
              TransitionComponent: Slide,
            });
          }
          setActiveQuestion(qData.question);
        });
      })
      .catch((err) => {
        setSessionData(null);
        utils.logOut();
        // history.push("/end");
        console.log(err);
      });

    (async () => {
      try {
        const temp = await utils.getTime();
        setTime(temp.data.time);
      } catch (e) {
        utils.logOut();
        setSessionData(null);
      }
    })();
  }, []);

  const checkAnswer = (id, ans) => {
    const nextId = getNextQuestionId(null, id);

    utils.getQuestion(nextId, id, ans).then((data) => {
      console.log(data);
      if (data) {
        utils.saveQuestionState(nextId);
        const message = "Correct Answer!!";
        enqueueSnackbar(message, {
          anchorOrigin: {
            vertical: "top",
            horizontal: "center",
          },
          variant: "success",
          TransitionComponent: Slide,
        });

        setScore(data.score);
        utils.saveSessionScore(data.score);

        if (nextId === -1) {
          utils.endContest().then((res) => {
            setTimeout(() => history.push("/finish", 1000));
          });
        } else {
          setActiveQuestion(data.question);
        }
      } else {
        const message = "Wrong Answer!!";
        enqueueSnackbar(message, {
          anchorOrigin: {
            vertical: "top",
            horizontal: "center",
          },
          variant: "error",
          TransitionComponent: Slide,
        });
      }
    });
  };

  return (
    <div className={classes.root}>
      <Grid
        container
        direction="row"
        justify="flex-start"
        alignItems="baseline"
      >
        <Grid item xs={4}>
          <Grid
            container
            direction="column"
            justify="space-around"
            alignItems="stretch"
            spacing={3}
          >
            <Grid item xs={12}>
              <Timer initTime={time} score={score} />
            </Grid>
            <Grid item xs={12}>
              <QuestionList list={list} active={activeQuestion} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={8}>
          <Question question={activeQuestion} checkAnswer={checkAnswer} />
        </Grid>
      </Grid>
    </div>
  );
};

export default MainPage;
