import { Grid, Typography, Card, CircularProgress } from "@material-ui/core";
import { useState, useEffect, useContext, useRef } from "react";
import utils from "../utils";
import SessionContext from "../SessionContext";
import { useSnackbar } from "notistack";
import Slide from "@material-ui/core/Slide";
import { useHistory } from "react-router-dom";

function useInterval(callback, delay = 1000) {
  const savedCallback = useRef();

  useEffect(() => {
    savedCallback.current = callback;
  });

  useEffect(() => {
    let id = setInterval(tick, delay);
    return () => clearInterval(id);

    function tick() {
      savedCallback.current(id);
    }
  }, [delay]);
}

const Timer = (props) => {
  const { score, initTime } = props;
  const [time, setTime] = useState(initTime);
  const history = useHistory();
  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    setTime(initTime);
  }, [initTime]);

  const tick = (id) => {
    if (time === null) return;

    if (time <= 1000) {
      clearInterval(id);
      utils.endContest().then((res) => {
        history.push("/finish");
        enqueueSnackbar("Time up!", {
          anchorOrigin: {
            vertical: "top",
            horizontal: "center",
          },
          variant: "warning",
          TransitionComponent: Slide,
        });
      });
    } else if (time >= 1000) {
      setTime(time - 1000);
    }
  };

  useInterval(tick);

  const timeObj = getTimeObject(time);
  return (
    <Grid
      container
      direction="row"
      justify="flex-start"
      alignItems="center"
      spacing={1}
    >
      <Grid item xs={5}>
        <Card style={{ padding: "5%" }}>
          <Typography variant="subtitle1">Time</Typography>

          <Typography variant="h4">
            {time ? (
              `${timeObj.h}:${timeObj.m}:${timeObj.s}`
            ) : (
              <CircularProgress />
            )}
          </Typography>
        </Card>
      </Grid>
      <Grid item xs={4}>
        <Card style={{ padding: "5%" }}>
          <Typography variant="subtitle1">Score</Typography>
          <Typography variant="h4">{score}</Typography>
        </Card>
      </Grid>
    </Grid>
  );
};

function getTimeObject(time) {
  if (!time) return;
  //Convert millseconds to hours, minutes, seconds
  let s = Math.floor(time / 1000) % 60;
  let m = Math.floor((time / (1000 * 60)) % 60);
  let h = Math.floor((time / (1000 * 60 * 60)) % 24);

  h = h < 10 ? `0${h}` : h;
  m = m < 10 ? `0${m}` : m;
  s = s < 10 ? `0${s}` : s;

  return {
    h,
    m,
    s,
  };
}

export default Timer;
